import React, { useState } from 'react';
import { Box, TextField, Button, MenuItem, Typography } from '@mui/material';
import WorkoutPlan from '../components/Workoutplan';

const Schedule = () => {
  const [age, setAge] = useState('');
  const [gender, setGender] = useState('');
  const [goals, setGoals] = useState([]);
  const [goalInput, setGoalInput] = useState('');
  const [userId, setUserId] = useState(null);
  const [scheduleData, setScheduleData] = useState(null);
  const [loading, setLoading] = useState(false);

  // Add a new goal to the goals list
  const addGoal = () => {
    if (goalInput && !goals.includes(goalInput)) {
      setGoals([...goals, goalInput]);
      setGoalInput('');
    }
  };

  // Handle form submission: POST to save user data, then GET the generated schedule
  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      // POST request to store user profile
      const postResponse = await fetch('http://localhost:5000/user-profile', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ age: parseInt(age), gender, goals })
      });
      const postData = await postResponse.json();
      setUserId(postData.userId);
      
      // GET request to retrieve user profile and generated schedule
      const getResponse = await fetch(`http://localhost:5000/user-profile/${postData.userId}/`);
      console.log(getResponse);
      const getData = await getResponse.json();
      console.log(getData);
      setScheduleData(getData);
    } catch (error) {
      console.error('Error submitting form:', error);
    }
    setLoading(false);
  };

  // If schedule data is available, display the generated workout schedule
  if (scheduleData) {
    return <WorkoutPlan schedule={scheduleData.schedule} user={scheduleData.user} />;
  }

  return (
    <Box sx={{ p: 4, maxWidth: 600, mx: 'auto' }}>
      <Typography variant="h4" gutterBottom>
        Enter Your Profile
      </Typography>
      <form onSubmit={handleSubmit}>
        <TextField
          label="Age"
          type="number"
          fullWidth
          value={age}
          onChange={(e) => setAge(e.target.value)}
          margin="normal"
          required
        />
        <TextField
          select
          label="Gender"
          fullWidth
          value={gender}
          onChange={(e) => setGender(e.target.value)}
          margin="normal"
          required
        >
          <MenuItem value="male">Male</MenuItem>
          <MenuItem value="female">Female</MenuItem>
          <MenuItem value="other">Other</MenuItem>
        </TextField>
        <TextField
          label="Add a Goal"
          fullWidth
          value={goalInput}
          onChange={(e) => setGoalInput(e.target.value)}
          margin="normal"
        />
        <Button variant="contained" onClick={addGoal} sx={{ mt: 1 }}>
          Add Goal
        </Button>
        <Box sx={{ mt: 2 }}>
          <Typography variant="subtitle1">Goals:</Typography>
          {goals.length === 0 ? (
            <Typography variant="body2">No goals added yet.</Typography>
          ) : (
            goals.map((goal, index) => (
              <Typography key={index} variant="body1">
                - {goal}
              </Typography>
            ))
          )}
        </Box>
        <Button
          type="submit"
          variant="contained"
          color="primary"
          fullWidth
          sx={{ mt: 3 }}
          disabled={loading || !age || !gender || goals.length === 0}
        >
          {loading ? 'Submitting...' : 'Submit'}
        </Button>
      </form>
    </Box>
  );
};

export default Schedule;
