import React from "react";
import {
  Container,
  Typography,
  Box,
  List,
  ListItem,
  Grid,
  Card,
  CardMedia,
  CardContent,
  Divider,
} from "@mui/material";

const WorkoutPlan = ({ schedule, user }) => {
  if (!schedule || !schedule.workoutSchedule || !schedule.workoutSchedule.days) {
    return (
      <Container maxWidth="md" sx={{ py: 4 }}>
        <Typography variant="h6" align="center">
          Loading workout plan...
        </Typography>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Typography variant="h3" gutterBottom>
        Weekly Workout Plan
      </Typography>
      <Typography variant="subtitle1" color="text.secondary" gutterBottom>
        Age: {user?.age || "N/A"} | Gender: {user?.gender || "N/A"}
      </Typography>

      <Box my={3}>
        <Typography variant="h5" gutterBottom>
          Goals:
        </Typography>
        {user?.goals && user.goals.length > 0 ? (
          <List>
            {user.goals.map((goal, idx) => (
              <ListItem key={idx} disableGutters>
                <Typography variant="body1">- {goal}</Typography>
              </ListItem>
            ))}
          </List>
        ) : (
          <Typography variant="body2">No goals specified.</Typography>
        )}
      </Box>

      <Divider sx={{ my: 3 }} />

      <Box my={3}>
        <Typography variant="h5" gutterBottom>
          Weekly Schedule:
        </Typography>
        {schedule.workoutSchedule.days.map((dayObj) => (
          <Box key={dayObj.day} mb={4}>
            <Typography variant="h6" color="primary">
              {dayObj.day} - {dayObj.focus}
            </Typography>
            {dayObj.exercises && dayObj.exercises.length > 0 ? (
              <Grid container spacing={2} mt={2}>
                {dayObj.exercises.map((exercise) => (
                  <Grid item xs={12} sm={6} key={exercise.id}>
                    <Card>
                      <CardMedia
                        component="img"
                        image={exercise.gifUrl}
                        alt={exercise.name}
                        height="200"
                      />
                      <CardContent>
                        <Typography variant="h6">{exercise.name}</Typography>
                        <Typography variant="body2">
                          <strong>Target:</strong> {exercise.primaryTarget}
                        </Typography>
                        <Typography variant="body2">
                          <strong>Secondary:</strong>{" "}
                          {exercise.secondaryTargets
                            ? exercise.secondaryTargets.join(", ")
                            : "N/A"}
                        </Typography>
                        <Typography variant="body2">
                          <strong>Sets:</strong> {exercise.sets} | <strong>Reps:</strong>{" "}
                          {exercise.reps}
                        </Typography>
                        <Typography variant="body2">
                          <strong>Rest:</strong> {exercise.rest}
                        </Typography>
                      </CardContent>
                    </Card>
                  </Grid>
                ))}
              </Grid>
            ) : (
              <Typography variant="body2" color="text.secondary" sx={{ fontStyle: 'italic', mt: 2 }}>
                Rest Day
              </Typography>
            )}
          </Box>
        ))}
      </Box>

      <Divider sx={{ my: 3 }} />

      <Box my={3}>
        <Typography variant="h5" gutterBottom>
          Trainer Notes:
        </Typography>
        {schedule.notes && schedule.notes.length > 0 ? (
          <List>
            {schedule.notes.map((note, idx) => (
              <ListItem key={idx} disableGutters>
                <Typography variant="body1">- {note}</Typography>
              </ListItem>
            ))}
          </List>
        ) : (
          <Typography variant="body2">No notes provided.</Typography>
        )}
      </Box>
    </Container>
  );
};

export default WorkoutPlan;
